import React from 'react';
//import ReactDOM from 'react-dom';
import { Component } from 'react';
import Option from './Option'



class Options extends Component{
    render (){
        return(
            <div className = 'Options'style={optionsStyle}>
            <ol type ='A' >
               <li><Option /></li>
               <li><Option /></li>
               <li><Option /></li>
            </ol>
               
            </div>
        );
    }
}

const optionsStyle = {
    width: '100%',
    margin: 'auto',
    padding: '10px'
    
}

export default Options;