import React from 'react';
//import ReactDOM from 'react-dom';
import { Component } from 'react';


class Answer extends Component{
    render (){
        return(
            <div className = 'Answer'>
            
                  <button style={AnswerStyle}>
                      Answer
                  </button>
              
            </div>
        );
    }
}

const AnswerStyle = {
    background: 'green',
    color:'white',
    padding: '10px',
    width: '50%',
    float: 'right',
    
}

export default Answer;