import React from 'react';
//import ReactDOM from 'react-dom';
import { Component } from 'react';


class Header extends Component{
    render (){
        return(
            <div className = 'Header'>
              <header style={headerStyle}>
                  <h1>CHOICE MAKER</h1>
              </header>
            </div>
        );
    }
}

const headerStyle = {
    background: 'darkblue',
    color:'white',
    padding: '10px',
    
}

export default Header;